<template>
  <div
    @click="mostrar = !mostrar"
    class="
      w-6
      h-6
      hijo
      flex-none flex
      items-center
      justify-center
      text-xs
      font-bold
      cursor-pointer
      relative
      text-black
    "
    :class="{
      cero: color === 0,
      uno: color === 1,
      dos: color === 2,
      tres: color === 3,
    }"
    :style="{ 'grid-column-start': semana, 'grid-column-end': semana }"
  >
    {{ valor }}

    <!-- ICONO REPORTADO -->
    <div
      v-if="reportado"
      class="
        absolute
        left-2
        top-4
        bg-green-300
        text-green-600
        flex
        items-center
        justify-center
        w-4
        h-4
        rounded-full
        shadow
        cursor-pointer
      "
    >
      R
    </div>

    <div
      v-if="mostrar && encabezado !== 'si'"
      class="
        absolute
        -top-full
        p-2
        bg-gray-300
        z-50
        w-max
        text-xs
        rounded-xl
        flex flex-col
        items-center
        justify-start
      "
    >
      <button class="bg-red-100 text-red-400 mb-2 w-full py-2 rounded-lg">
        Cerrar
      </button>

      <div class="w-full text-black">
        <div
          @click="
            mostrarOT = !mostrarOT;
            detallesActividad(item.idRegistro);
          "
          v-for="(item, index) in arrayActividades"
          :key="index"
          class="actividad relative"
        >
          <h1>{{ item.fecha }}</h1>
          <h1 class="mx-2">-</h1>
          <h1>{{ item.creadoPor }}</h1>

          <!-- ICONO REPORTADO -->
          <div
            v-if="item.reportado === 1"
            class="
              absolute
              -left-3
              top-1
              bg-green-300
              text-green-600
              flex
              items-center
              justify-center
              w-4
              h-4
              rounded-full
              shadow
              cursor-pointer
            "
          >
            R
          </div>
        </div>
      </div>
    </div>

    <!-- SHOW OT -->
    <teleport to="#modales" v-if="mostrarOT">
      <div
        class="
          absolute
          top-0
          pt-24
          flex flex-col
          items-center
          justify-start
          z-50
          overflow-y-auto
          w-full
          h-screen
          bg-black
        "
        v-for="(item, index) in detalles"
        :key="index"
      >
        <!-- CERRAR OT -->
        <div
          class="
            absolute
            top-0
            py-2
            px-4
            rounded-b-3xl
            bg-gray-300
            opacity-60
            cursor-pointer
          "
          @click="mostrarOT = !mostrarOT"
        >
          <h1>Clic para cerrar</h1>
        </div>

        <div class="w-full md:w-1/4 flex flex-col items-center justify-start">
          <div
            class="
              px-4
              pb-4
              w-full
              flex flex-col
              items-start
              justify-start
              text-left
            "
          >
            <div class="mb-4 text-white">
              <h1 class="font-bold uppercase">
                {{ item.equipo }}
              </h1>
              <h1 class="font-bold uppercase">
                {{ item.sabana }}
              </h1>
              <h1 class="font-bold uppercase">
                Por: <span class="italic">{{ item.creadoPor }}</span>
              </h1>
              <h1 class="font-bold uppercase">
                El: <span class="italic">{{ item.fechaCreado }}</span>
              </h1>
            </div>

            <!-- Listado -->
            <div
              class="
                flex flex-col
                items-center
                justify-start
                w-full
                shadow
                rounded-xl
              "
            >
              <div
                class="
                  flex flex-col
                  items-center
                  justify-start
                  w-full
                  shadow
                  rounded-xl
                "
              >
                <SemanaApartadoSabanas
                  v-for="(item, index) in item.apartados"
                  :key="index"
                  :apartado="item.apartado"
                  :array="item"
                />
              </div>
            </div>
            <!-- Listado -->
          </div>
        </div>
      </div>
    </teleport>
    <!-- SHOW OT -->
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import SemanaApartadoSabanas from "./SemanaApartadoSabanas.vue";
import { useStore } from "vuex";
export default {
  components: { SemanaApartadoSabanas },
  props: {
    array: {
      type: Object,
      value: [],
    },

    semana: {
      type: Number,
      required: true,
    },

    cantidad: {
      type: Number,
      required: false,
    },

    encabezado: {
      type: String,
      required: false,
    },
  },

  setup(props) {
    const store = useStore();
    const valor = ref();
    const mostrar = ref(false);
    const mostrarOT = ref(false);
    const color = ref(0);
    const detalles = ref([]);
    const reportado = ref(false);
    const arrayActividades = ref([]);

    if (props.encabezado == "si") {
      props.cantidad > 0 ? (valor.value = props.cantidad) : "";
      color.value = 3;
    }

    const detallesActividad = async (idRegistro) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "detallesActividad",
        idRegistro: idRegistro,
      });

      if (response.response) detalles.value = response.data.data;

      if (!response.response) detalles.value = [];
    };

    if (props.array.registros) {
      let cantidad = 0;
      props.array.registros.forEach((element) => {
        if (element.semana == props.semana) {
          arrayActividades.value.push(element);

          if (props.encabezado == "no") {
            cantidad++;
            color.value = element.color;

            if (element.reportado === 1) reportado.value = true;
          }
        }
      });

      if (cantidad > 0) valor.value = cantidad;
      else valor.value = "";
    }

    return {
      valor,
      mostrar,
      mostrarOT,
      arrayActividades,
      color,
      detallesActividad,
      detalles,
      reportado,
    };
  },
};
</script>

<style scoped>
.hijo {
  grid-row-start: 1;

  border-width: 0.16rem;

  border-radius: 4px;
  margin-right: 2px;
}

.cero {
  border-color: #414141;
  color: #414141;
  background-color: rgb(255, 255, 255);
}

.uno {
  border-color: #80c722;
  color: #80c722;
  background-color: rgb(230, 255, 215);
}

.dos {
  border-color: #ff9d42;
  color: #ff9d42;
  background-color: rgb(255, 234, 215);
}

.tres {
  border-width: 0;
}

.tres:hover {
  background-color: whitesmoke;
}

.actividad {
  @apply flex flex-row items-center justify-start text-left w-full py-2 bg-white px-2 text-xs font-semibold border-b;
}

.actividad:hover {
  @apply bg-blue-100;
}

.actividad:last-child {
  @apply border-b-0 rounded-b-xl;
}

.actividad:first-child {
  @apply rounded-t-xl;
}
</style>
